import Highlighter from "react-highlight-words"
import { ShareIcon } from "@rp/react/web/common/base-components"
import { Box, Typography, useTheme } from "@rp/react/web/mui/core"
import { EllipsisTooltip } from "../EllipsisTooltip"

type Props = {
  width: number | string
  text: string
  filterTerm: string
  showCompanyIcon?: boolean
  secondaryText: string
  isMobile: boolean
}

function HubTableRowFirst({
  width,
  text,
  filterTerm,
  showCompanyIcon,
  secondaryText,
  isMobile,
}: Props) {
  const theme = useTheme()
  return (
    <Box
      sx={{
        display: "flex",
        flexDirection: "column",
        flexGrow: 1,
        width,
      }}
    >
      <Box sx={{ width: "100%", display: "flex", height: "100%", alignItems: "center" }}>
        <EllipsisTooltip
          tooltipText={text ?? ""}
          typographyProps={{ fontSize: 14, fontWeight: isMobile ? 700 : 400 }}
        >
          <Highlighter
            autoEscape
            highlightStyle={{ color: theme.palette.primary.light }}
            highlightTag="span"
            searchWords={[filterTerm]}
            textToHighlight={text ?? ""}
          />
        </EllipsisTooltip>
        {showCompanyIcon ? <ShareIcon shared sx={{ mx: 1 }} /> : null}
      </Box>
      {isMobile && (
        <Typography
          sx={{
            fontSize: 12,
            color: "grey.300",
            overflow: "hidden",
            whiteSpace: "nowrap",
            textOverflow: "ellipsis",
          }}
        >
          {secondaryText}
        </Typography>
      )}
    </Box>
  )
}

export { HubTableRowFirst }
